body {
    margin: 0;
    font-family: "Montserrat_Regular";
    font-style: normal;
    background: rgb(245, 248, 254);
    ul {
        padding: 0;
        margin: 0;
        li {
            list-style: none;
        }
    }
    a {
        text-decoration: none;
    }
    h1,
    h2,
    h3,
    h4,
    h5,
    h6,
    p {
        margin: 0;
    }
    .main {
        max-width: 95%;
        margin: auto;
        // background-color: #022144;
    }
}

@media(min-width: 720px) {
    body {
        padding: 0;
        .header {
            padding: 0;
        }
        .main {
            max-width: 80%;
            margin: auto;
            padding: 0;
            padding-top: 16px;
            .first_section {
                display: grid;
                grid-template-columns: repeat(2, 1fr);
                margin-bottom: 20px;
            }
        }
    }
}